import React from "react";
import "./SearchHandler.css";
import Fade from "react-reveal/Fade";
import LinkButton from "../../Shared/Components/Navigation/LinkButton";
import TextField from "@material-ui/core/TextField";
import ButtonGroup from "@material-ui/core/ButtonGroup";

import "../../Shared/Typography/typography.css";

class SearchHandler extends React.Component {
  constructor(props) {
    super(props);
    this.state = { value: "" };

    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleChange(event) {
    this.setState({ value: event.target.value });
  }

  handleSubmit(event) {
    var query = this.state.value;
    console.log(query);
    return query.toString();
  }

  render() {
    return (
      <div class="form-group search-bar vertical-center mt-5 ">
        <div class="input-group">
          <Fade top>
            <TextField
              type="text"
              id="myInput"
              className="input_bar form-control"
              onKeyDown={this.handleKeyDown}
              color="secondary"
              value={this.state.value}
              onChange={this.handleChange}
              label="Search"
              autocomplete="on"
            />
            <span>
              <ButtonGroup
                color="dark"
                variant="text"
                aria-label="outlined secondary button group"
                className=""
              >
                <LinkButton
                  className="pBody mt-2"
                  type="submit"
                  value="Search"
                  color="dark"
                  to={`./search/papers/${this.state.value}`}
                >
                  Search papers
                </LinkButton>
                <LinkButton
                  className="pBody  mt-2"
                  type="submit"
                  value="Search"
                  color="dark"
                  to={`./search/conferences/${this.state.value}`}
                >
                  Search conferences
                </LinkButton>
              </ButtonGroup>
            </span>
          </Fade>
        </div>
      </div>
    );
  }
}

export default SearchHandler;
